.App {
  text-align: center;
  max-width: 600px;
  height: 100vh;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container{
  left: 0;
  right: 0;
  height: 45px;
  position: absolute;
  bottom: 100px;
}
.content{
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
}
.text{
  flex: auto;
  outline: none;
  border-right: none;
  border-color: #61dafb;
}
.btn{
  width: 80px;
  height: 100%;
  background-color: #61dafb;
  border: none;
  outline: none;
}
.message{
  background-color: aqua;
  padding: 20px;
  min-height: 200px;
}
